.caption {
    $this:&;

    line-height: 1;

    &__key {
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 2px;
        margin-right: 3px;
    } 

    &__value {
        font-size: 12px;
        font-style: italic;
    }

    &__divider {
        display: none;
    }

    &--property-article {
        #{$this}__key {
            margin-right: 0;
        }

        #{$this}__divider {
              display: inline;
              font-size: 10px;
        }
    }
}