.article-layout {
    $this: &;

    &__container {
        max-width: var(--container-width);
        margin-top: 75px;
        margin-right: auto;
        margin-left: auto;
        background-color: var(--theme-color-bg);

        @include desktop {
            margin-top: 0;
        }
    }

    &__intro {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 1470px;
        margin: 0 auto 60px;

        @include tablet {
            padding: 0 36px;
        }
        
        @include desktop {
            padding: 0 80px;
        }

        @include desktop-medium {
            margin-top: 154px;
            margin-bottom: 150px;
        }
    }

    &__intro-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 36px;
        margin-bottom: 70px;


        @include tablet {
            padding: 0;
        }

        @include desktop-medium {
            margin-bottom: 140px;
        }
    }

    &__title {
        font-family: $ff-financier-display;
        font-size: 46px;
        font-weight: 300;
        line-height: 1;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 25px;

        @include desktop {
            font-size: 120px;
            margin-bottom: 50px;
        }
    }

    &__cols {
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 40px;
            width: 100%;
        }

        @include desktop {
            gap: 80px;
        }
        
        @include desktop-medium {
            gap: 160px;
        }
    }

    &__img {
        @include objectFitImageParent;

        position: relative;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(500/375);
        }
    }

    &__col {
        @include tablet {
            width: 50%;
        }

        &--left {
            margin-bottom: 56px;

            @include tablet {
                margin-bottom: 0;
            }

            @include desktop-medium {
                width: 58%;
            }
        }

        &--right {
            padding: 0 35px;

            @include tablet {
                padding: 0;
            }

            @include desktop-medium {
                width: 42%;
            }
        }
    }

    &__share {
        @include tablet {
            display: none;
        }
    }

    &__credits {
        margin-bottom: 30px;
    }

    &__summary {
        font-family: $ff-financier-display;
        font-size: 26px;
        line-height: 1.2;

        @include desktop-medium {
            font-size: 40px;
        }
    }

    &--property {
        #{$this}__intro {
            max-width: none;

            @include desktop {
                margin-top: 50px;
            }
        }

        #{$this}__title {
            @include desktop {
                margin-bottom: 38px;
            }
        }
    
        #{$this}__intro-wrapper{
            @include desktop {
                margin-bottom: 44px;
            }
        }

        #{$this}__cols {
            @include tablet {
                gap: 10px;
            }

            @include desktop {
                gap: 50px;
            }
        }

        #{$this}__col {
            &--right {
                order: 2;
                padding: 0;
                margin-bottom: 60px;

                @include tablet {
                    margin-right: -36px;
                    margin-bottom: 0;
                }

                @include desktop {
                    margin-right: -87px;
                    width: auto;
                }
            }

            &--left {
                order: 1;
                padding: 0 36px;
                margin-bottom: 0;

                @include tablet {
                    padding: 0;
                    margin-top: 40px;
                }

                @include desktop {
                    width: auto;
                    max-width: 532px;
                    margin-left: 30px;
                }

                @include desktop-wide {
                    margin-top: 0;
                    margin-left: 140px;
                }
            }
        }

        #{$this}__img {
            &--main {
                width: 100%;
                &::before {
                    padding-top: percentage(405/375);

                    @include tablet {
                        padding-top: percentage(1024/1676);
                    }
                }
            }

            &--secondary {
                max-width: 260px;
                margin-top: -55px;
                margin-left: auto;

                @include tablet {
                    max-width: none;
                }

                @include desktop {
                    min-width: 420px;
                }

                @include desktop-medium {
                    min-width: 500px;
                }

                @include desktop-wide {
                    min-width: 762px;
                    margin-top: -94px;
                }

                &::before {
                    padding-top: percentage(270/259);
                    
                    @include desktop {
                        padding-top: percentage(890/762);
                    }
                }
            }
        }
    }

    &--no-secondary-img {
        #{$this}__cols {
            margin-top: 60px;
            
            @include desktop-wide {
                margin-top: 200px;
            }
        }

        #{$this}__col {
            &--right {
                margin: 0;
            }
            
            &--left{
                margin-top: 0;
            }
        }
    }
}
