.share-mobile {
    $this: &;

    @include btn;

    width: 100%;
    padding: 12px 0;
    font-size: 8px;
    text-transform: uppercase;
    border-top: 1px solid var(--color-grey);

    &__icon {
        margin-right: 8px;
    }

    @include tablet {
        display: none;
    }
}