.article-card {
    $this: &;

    @include link-reset;

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(450/389);
        }

        img {
            transition: transform 0.3s ease-out;
        }
    }

    &__icon-wrap {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
    }

    &__content {
        overflow: hidden;
        margin-top: 14px;
        font-size: 16px;
        padding: 10px 20px 6px;

        @include desktop {
            padding: 20px;
        }
    }

    &__title {
        overflow: hidden;
        display: -webkit-box;
        font-size: 25px;
        line-height: 1.16;
        font-weight: $fw-light;
        font-family: $ff-financier-display;
        margin-top: 12px;

        @include desktop {
            font-size: 30px;
        }
    }

    &__label {
        font-size: 8px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        line-height: 1.4;
        letter-spacing: 2px;
        text-transform: uppercase;

        @include desktop {
            font-size: 10px;
        }
    }

    @include hover-focus {
        & #{$this}__media img {
            transform: scale(0.95);
        }
    }
}
