.quote-block {
    display: flex;
    flex-direction: column-reverse;
    gap: 38px;
    margin-bottom: 40px;

    @include desktop {
        flex-direction: row;
        margin-bottom: 80px;
    }

    &__col1 {
        display: flex;
        justify-content: center;
        align-items: center;

        @include desktop {
            margin-left: percentage(143/1676);
            width: percentage(399/1676);
            margin-right: percentage(183/1676);

            .quote-block--no-image & {
                width: 100%;
                max-width: 818px;
                margin-right: 0;
            }
        }
    }

    &__col2 {
        @include objectFitImageParent;
        position: relative;
        overflow: hidden;
        z-index: 0;

        @include desktop {
            width: percentage(961/1676);
        }

        &::before {
            content: '';
            display: block;
            padding-top: percentage(760/961);
        }
    }

    blockquote {
        padding: 0;
        margin: 0;
        max-width: 818px;

        p {
            margin: 0;
            line-height: 1.25;
            font-size: 26px;
            font-family: $ff-financier-display;
            font-weight: $fw-light;

            &::before {
                content: '“';
                display: inline;
                font-family: $ff-financier-display;
                font-weight: $fw-light;
            }

            &::after {
                content: '”';
                display: inline;
                font-family: $ff-financier-display;
                font-weight: $fw-light;
            }

            @include desktop {
                font-size: 30px;
            }

            @include desktop-wide {
                font-size: 40px;
            }
        }
    }

    figure {
        margin: 0 35px;

        @include desktop {
            margin: 0;
        }
    }

    figure figcaption {
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        font-weight: $fw-regular;
        font-style: italic;
        line-height: 1.4;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding-left: 40px;
        margin-top: 25px;
    }
}
