.tag-page {

    &__title {
        margin-bottom: 20px;

        @include desktop {
            margin-bottom: 12px;
        }
    }

    &__qty-found {
        text-align: center;
        font-size: 8px;
        line-height: 1.4;
        text-transform: uppercase;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-medium;
        margin-bottom: 50px;
    }

    &__title-latest {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 30px;
        line-height: 1.25;
        margin-bottom: 28px;

        @include desktop {
            font-size: 40px;
            margin-bottom: 42px;
        }
    }

    &__separator {
        height: 0;
        width: 100%;
        margin: 25px auto 50px;
        border: none;
        border-bottom: 2px solid $color-grey3;

        @include desktop {
            width: percentage(1390/1678);
            margin: 75px auto 100px;
        }
    }

    &__breadcrumb {
        display: none;

        @include desktop {
            display: block;
            margin-top: 35px;
        }
    }
}
