.sidebar {
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    border-bottom: 1px solid var(--color-grey);
    height: var(--top-menu-height);
    background-color: $color-white;
    z-index: 1;

    padding: 15px 25px;

    @include tablet {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        width: var(--sidebar-width);
        height: var(--viewport-height);
        border-left: 1px solid var(--color-grey);
    }

    &__the-view-title {
        font-size: 30px;
        line-height: 1;

        @include tablet {
            display: none;
        }
    }

    &__footer {
        display: none;

        @include tablet {
            display: flex;
            justify-content: center;
            margin-bottom: 18px;
        }
    }
}
