.btn {
    @include btn;

    font-size: 10px;
    font-weight: 500;
    letter-spacing: 2.4px;
    padding: 16px 25px;
    border: 1px solid var(--color-grey);

    @include desktop {
        font-size: 12px;
    }
}

.link {
    @include link-reset;
    
    position: relative;
    font-family: $ff-helvetica-neue;
    text-transform: uppercase;
    letter-spacing: 2px;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        transform: scaleX(0);
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: var(--color-black);
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
    }

    &:hover:after {
        transform: scaleX(1);
        transform-origin: bottom left;
    }

    &:active {
        opacity: 0.6;
    }
}
