.footer-nav {

    font-family: $ff-financier-display;
    font-weight: $fw-light;
    font-style: italic;
    font-size: 16px;

    @include desktop {
        font-size: 18px;
    }

    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    &__item {

        &::before {
            content: '|';
            font-style: italic;
            padding: 0 10px;
        }

        &:first-child {
            &:before {
                display: none;
            }
        }

        @include desktop {
            &:first-child {
                &:before {
                    display: inline;
                }
            }
        }

    }

    &__link {
        color: $color-black;
        @include linkhover($color-black);
    }
}
