.top-nav {
    display: none;

    @include desktop {
        z-index: -1;
        position: sticky;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        max-width: var(--container-width);
        margin: 0 auto;
        padding: 42px 87px;
    }

    &__breadcrumbs {
        @include desktop {
            width: 33%;
        }
    }

    &__the-view-logo {
        @include desktop {
            display: flex;
            justify-content: center;
            width: 33%;
        }
    }

    &__date {
        @include desktop {
            width: 33%;
            text-align: right;
            font-family: $ff-helvetica-neue;
            font-size: 10px;
            text-transform: uppercase;
            letter-spacing: 2px;
        }
    }
}