.footer {
    $this:&;
    width: 100%;

    margin: 37px 0 33px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    @include desktop {
        flex-direction: row;
        align-items: center;
    }


    &__logo {
        margin-bottom: 25px;
        @include desktop {
            margin-bottom: 0;
        }
    }

    &__nav-copy {
        @include desktop {
            display: flex;
        }
    }

    &__copyright {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-style: italic;
        font-size: 16px;
        margin-bottom: 5px;

        @include desktop {
            font-size: 18px;
        }
    }

    &__back-to-top {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);

        width: 35px;
        height: 35px;
        border-radius: 50%;
        border: 1px solid $color-black;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            transform: rotate(-90deg);
        }

        @include desktop {
            display: none;
        }
    }
}

.footer-container {
    width: 100%;
}

.footer-wrapper {
    background-color: $color-white;
    border-top: 2px solid $color-grey4;
}
