.error-404 {
    $this:&;

    max-width: 1390px;
    margin: 0 auto;
    padding: 75px 0 100px;

    @include desktop {
        display: flex;
    }

    @include desktop-medium {
        padding: 166px 0 112px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 50px;
        text-transform: uppercase;
        
        @include desktop {
            align-items: flex-start;
            width: 41%;
            margin-right: 30px;
            margin-bottom: 0;
        }
    }

    &__caption {
        margin-bottom: 10px;
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        font-weight: 500;
        letter-spacing: 2px;
    }

    &__title {
        margin-bottom: 38px;
        font-family: $ff-financier-display;
        font-size: 45px;
        font-weight: lighter;
        line-height: 1;
        text-align: center;

        @include desktop {
            margin-bottom: 50px;
            text-align: left;
        }

        @include desktop-medium {
            font-size: 86px;
        }

        @include desktop-wide {
            font-size: 100px;
        }
    }

    &__btns {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include desktop {
            align-items: flex-start;
        }
    }

    &__btn {
        & + & {
            margin-top: 25px;
        }
    }

    &__video {
        min-height: 316px;
        width: 100%;
        object-fit: cover;

        @include desktop {
            width: 59%;
            min-height: 460px;
        }
    }
}
