.article-cards-grid {
    grid-template-columns: 1fr;
    display: grid;
    gap: 38px;

    @include tablet {
        grid-row-gap: 33px;
        grid-template-columns: 1fr 1fr;
    }

    @include desktop {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    &--article-layout {
        margin-bottom: 40px;

    }
}
