.breadcrumbs {
    $this: &;

    &__list {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        display: flex;
        align-items: center;
        white-space: nowrap;
    }

    &__link {
        font-size: 10px;
    }

    &__current {
        font-size: 10px;
        font-family: $ff-helvetica-neue;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-weight: 700;
        font-style: italic; 
    }

    &__icon {
        margin: 0 14px;
    }
}
