$hamburger-padding-x                       : 10px !default;
$hamburger-padding-y                       : 0 !default;
$hamburger-layer-width                     : 38px !default;
$hamburger-layer-short-width               : 25px !default;
$hamburger-layer-height                    : 2px !default;
$hamburger-layer-spacing                   : 10px !default;
$hamburger-layer-color                     : $color-grey!default;
$hamburger-layer-border-radius             : 0 !default;

.hamburger {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    font: inherit;
    color: inherit;
    text-transform: none;
    background-color: transparent;
    border: 0;
    margin: 0;
    overflow: visible;
}

.hamburger-box {
    width: $hamburger-layer-width;
    height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;
    display: inline-block;
    position: relative;
}

.hamburger-inner {
    display: block;
    top: 50%;
    margin-top: $hamburger-layer-height / -2;

    &,
    &::before,
    &::after {
        width: $hamburger-layer-width;
        height: $hamburger-layer-height;
        background-color: $hamburger-layer-color;
        border-radius: $hamburger-layer-border-radius;
        position: absolute;
        transition-property: transform;
        transition-duration: 0.15s;
        transition-timing-function: ease;
    }

    &::before,
    &::after {
        content: "";
        display: block;
    }

    &::before {
        top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
    }

    &::after {
        width: $hamburger-layer-short-width;
        bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
        right: 0;
        left: auto;
    }
}

.hamburger--squeeze {
    .hamburger-inner {
        transition-duration: 0.1s;
        transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);

        &::before {
            transition: top 0.1s 0.14s ease,
                opacity 0.1s ease;
        }

        &::after {
            transition: bottom 0.1s 0.14s ease,
                transform 0.1s cubic-bezier(0.55, 0.055, 0.675, 0.19);
        }
    }

    &.is-active {
        .hamburger-inner {
            transform: rotate(90deg);
            transition-delay: 0.14s;
            transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            left: -4px;

            &::before {
                top: 0;
                opacity: 0;
                transition: top 0.1s ease,
                    opacity 0.1s 0.14s ease;
            }

            &::after {
                bottom: $hamburger-layer-spacing;
                width: $hamburger-layer-width;
                transition: bottom 0.1s ease,
                    transform 0.1s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
            }
        }
    }
}
