.tags {
    $this: &;

    &__tag {
        font-size: 8px;

        & + & {
            position: relative;
            margin-left: 20px;

            &:before {
                content: '';
                position: absolute;
                top: 1px;
                left: -10px;
                height: 8px;
                width: 0.5px;
                background-color:var(--color-grey);
            }
        }

        @include desktop {
            font-size: 10px;
        }
    }
}